export default {
  data() {
    return {
      mountains: [],
    };
  },
  activated() {
    if (this.$fetchState.timestamp <= Date.now() - 30000) {
      this.$fetch();
    }
  },

  async fetch() {
    this.mountains = await this.$http.$get("https://api.nuxtjs.dev/mountains");
  },
  nuxtI18n: {
    paths: {
      en: "/mountains/",
      tr: "/daglar/",
    },
  },
};
